*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

.header{
    width: 100%;
    line-height: 100px;
    background-color: #f5ba13;
    display: flex;
    justify-content: left;
    align-items: left;
    padding-left: 50px;
    color: white;
    box-shadow: 5px 3px 15px -5px rgba(0, 0, 0, 0.5);
}

.header .noteicon{
    margin-top: 10px;
}
.header h1{
    font-size: medium;
    margin-top: 15px;
    margin-left: 5px;
}

.main_note{
    width: 500px;
    padding: 15px 10px 5px 10px;
    margin: 20px auto;
}

.my_note h1{
    color: #34495e;
}

.my_note p{
    color: grey;
}

form{
    min-height: 50px;
    color: #ecf0f1;
    padding: 15px 10px 5px 10px;
    box-shadow: 3px 5px 15px -5px rgba(0, 0, 0, 0.5);
    border-radius: 10px;
    position: relative;
}

input{
    width: 100%;
    line-height: 30px;
    background: transparent;
    border: none;
    outline: none;
    font-size: 1.5rem;
    margin: 10px 0;
    font-weight: bold;
}

textarea{
    width: 100%;
    line-height: 30px;
    background: transparent;
    border: none;
    outline: none;
    font-size: 1rem;;
    margin: 10px 0;
}

.MuiButton-root{
    width: 30px;
    height: 50px;
    position: absolute;
    bottom: -28px;
    left: 400px;
    cursor: pointer;
    background-color: #fff !important;
    box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.5);
    border-radius: 50% !important;
}

.MuiButton-root .plus_sign{
    font-size: 45px !important;
    font-weight: 900;
    color: #f4b400;
    cursor: pointer;
    z-index: 222;
    background: transparent;
}

.MuiButton-root:hover{
    background: #f4b400 !important;
}

.MuiButton-root:hover .plus_sign{
    color: #fff;
    background: transparent;
}

.note{
    background: #fff;
    border-radius: 7px;
    box-shadow: 0 2px 5px #ccc;
    padding: 10px;
    width: 240px;
    margin: 16px;
    float: left;
}

.note h1{
    font-size: 1.1em;
    margin-bottom: 6px;
}

.note p{
    font-size: 1.1em;
    margin-bottom: 10px;
    white-space: pre-wrap;
    word-wrap: break-word;
}

.note .btn{   
    position: relative;
    float: right;
    margin-right: 10px;
    color: #f4b400;
    border: none;
    height: 50px;
    width: 50px;
    background: transparent;
    cursor:pointer;
    outline:none;
    box-shadow: 3px 3px 15px -10px rgba(0, 0, 0, 0.5);
    border-radius: 50%;
}
.deleteIcon{
    width: 0.7em;
    height: 0.7em;
}

.note .btn:hover{
    background-color: #f5ba13;
}

.note .btn:hover .deleteIcon{
    background-color: #fff;
}

footer{
    width: 100%;
    position: absolute;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}
